@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: "Nunito";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  position: relative;
  display: flex;
  align-items: center;
  height: 60px;
  margin-bottom: 30px;
}

.links {
  display: flex;
  position: relative;
  height: 100%;
  align-items: center;
}

nav a {
  position: relative;
  margin: 0 20px;
  text-decoration: none;
}

.slider {
  height: 5px;
  position: absolute;
  bottom: 10px;
  left: 5%;
  border-radius: 10px;
  background-color: red;
}

.top-bar {
  display: flex;
  justify-content: space-between;
}

.logo {
  width: 130px;
  height: 100px;
}

.link {
  display: flex;
  flex-direction: column;
  position: relative;
}

.link > div {
  border-radius: 2px;
  position: absolute;
  bottom: -10px;
}

.link-active > div {
  height: 4px;
  width: 0%;
  animation: drawBorder 0.8s ease forwards;
}

@keyframes drawBorder {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

.link-active > div {
  background-size: 100% 2px;
}

.main {
  padding: 8vh 12vh;
  position: fixed;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  /* overflow-x: hidden; */
}

.main-text {
  margin: 20px 0px;
}

.sub-text {
  width: 95%;
  margin-bottom: 60px;
}

.art-1 {
  width: 100%;
  height: 100%;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 20px;
  width: 100%;
  text-align: center;
}

.button-link {
  text-decoration: none;
}

